.main-nav {
  width: 320px;
  margin: 0 auto;
  position: relative;
  min-height: 55px;
  background-color: #81b3d2; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none; }
  .main-nav__toggle:active, .main-nav__toggle:hover {
    background-color: #81b3d2; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2; }

.main-nav--closed .main-nav__toggle:active::before {
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2; }

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto; }
  .main-nav--opened .main-nav__toggle::after, .main-nav--opened .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #ffffff; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }
  .main-nav--opened .main-nav__toggle:active::after, .main-nav--opened .main-nav__toggle:active::before {
    background-color: #81b3d2; }

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none; }
  --active {
    display: block; }
  .main-nav--closed .site-list__item--active a {
    padding-right: 100px;
    padding-left: 100px;
    text-align: center; }
  .main-nav--opened {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    min-height: 220px;
    background-color: #81b3d2; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__list {
    position: static;
    min-height: 0; }
  .main-nav--nojs .site-list__item {
    display: block; }
  .main-nav--nojs .site-list__item--active a {
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center; } }

